import React, { useState } from "react";
import { Link } from "gatsby";
import Lightbox from "react-image-lightbox";

import "react-image-lightbox/style.css";
import productImg1 from "../../assets/images/productGallery/Rectangle 18.png";
import productImg2 from "../../assets/images/productGallery/Rectangle 19.png";
import productImg3 from "../../assets/images/productGallery/Rectangle 20.png";
import productImg4 from "../../assets/images/productGallery/Rectangle 21.png";
import productImg5 from "../../assets/images/productGallery/Rectangle 18 (0).png";
import productImg6 from "../../assets/images/productGallery/Rectangle 18 (1).png";
import productImg7 from "../../assets/images/productGallery/Rectangle 18 (2).png";
import productImg8 from "../../assets/images/productGallery/Rectangle 18 (3).png";
import productImg9 from "../../assets/images/productGallery/Rectangle 18 (4).png";
import productImg10 from "../../assets/images/productGallery/Rectangle 18 (5).png";
import productImg11 from "../../assets/images/productGallery/Rectangle 18 (6).png";
import productImg12 from "../../assets/images/productGallery/Rectangle 18 (7).png";
import productImg13 from "../../assets/images/productGallery/Rectangle 18 (8).png";
import productImg14 from "../../assets/images/productGallery/Rectangle 18 (9).png";
import productImg15 from "../../assets/images/productGallery/Rectangle 18 (10).png";
import productImg16 from "../../assets/images/productGallery/Rectangle 18 (11).png";

import "./productGallery.css";
import CallToAction from "../master/cta";

const images = [
  productImg1,
  productImg2,
  productImg3,
  productImg4,
  productImg5,
  productImg6,
  productImg7,
  productImg8,
  productImg9,
  productImg10,
  productImg11,
  productImg12,
  productImg13,
  productImg14,
  productImg15,
  productImg16,
];

const ProductGallery = () => {
  const [open, setOpen] = useState(undefined);
  return (
    <div className="home-content">
      <div className="header-title">
        <div className="container">
          <div className="header-title-text">
            <h2>
              Our <span>Gallery</span>
            </h2>
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/gallery">/ Gallery</Link>
              </li>
              <li>/ Product Gallery</li>
            </ul>
          </div>
        </div>
      </div>
      <div className="productGallery-section">
        <div className="container">
          <div className="productGallery-grid-section">
            {images.map((img, i) => (
              <div className="productGallery-box" key={i}>
                <img src={img} alt="galleryImg" onClick={() => setOpen(i)} />{" "}
              </div>
            ))}

            {typeof open !== "undefined" && (
              <Lightbox
                mainSrc={images[open]}
                nextSrc={images[(open + 1) % images.length]}
                prevSrc={images[(open + images.length - 1) % images.length]}
                onCloseRequest={() => setOpen()}
                onMovePrevRequest={() => {
                  console.log("open =>", open);
                  setOpen((open) => (open + images.length - 1) % images.length);
                }}
                onMoveNextRequest={() => {
                  console.log("open =>moce", open);
                  setOpen((open) => (open + 1) % images.length);
                }}
              />
            )}
          </div>
        </div>
      </div>
      <CallToAction />
    </div>
  );
};

export default ProductGallery;
